import { getStorage, setStorage, removeStorage } from '@/utils/storage'
const state = {
  histroyRecode: getStorage('histroyRecode') ? getStorage('histroyRecode') : []
}
const mutations = {
  addHistroyRecode (state, Info) {
    // 判断有无存储
    const res = state.histroyRecode
    // 检查是否存在
    const index = res.indexOf(Info)
    // 如果重复了则删除
    if (index !== -1) res.splice(index, 1)
    // 都需要添加
    res.unshift(Info)
    // 最后持久化存储
    setStorage('histroyRecode', res)
  },
  //   删除历史记录
  delHistroyRecode (state, Info) {
    state.histroyRecode.splice(state.histroyRecode.indexOf(Info), 1)
    setStorage('histroyRecode', state.histroyRecode)
  },
  //   删除所有历史记录
  delAllHistroyRecode (state) {
    state.histroyRecode = []
    removeStorage('histroyRecode')
  }
}
const actions = {}
const getters = {}

export default {
  namespaced: true,
  state,
  mutations,
  actions,
  getters
}
