import tokenRequest from '@/utils/tokenRequest'

// 获取购物车列表
export function getCartItem () {
  return tokenRequest.get('/cart/list')
}

// 加入购物车
export function addCart (data) {
  return tokenRequest.post('/cart/add', {
    ...data
  })
}

// 修改购物车
export function updateCart (obj) {
  return tokenRequest.post('/cart/update', obj)
}

// 删除购物车物品
export function deleteCartItem (obj) {
  return tokenRequest.post('/cart/clear', {
    cartIds: obj
  })
}
