const layout = () => import('@/views/layout/one/layout')
const home = () => import('@/views/layout/two/home.vue')
const classfiction = () => import('@/views/layout/two/classfiction.vue')
const cart = () => import('@/views/layout/two/cart.vue')
const my = () => import('@/views/layout/two/my.vue')

export default [
  {
    path: '/',
    component: layout,
    // 路由重定向
    redirect: '/home',
    children: [
      {
        path: 'home',
        component: home
      },
      {
        path: 'classfiction',
        component: classfiction
      },
      {
        path: 'cart',
        component: cart
      },
      {
        path: 'my',
        component: my
      }
    ]
  }
]
