<template>
  <div class="boxTitle">
    <div class="title" :style="{backgroundColor: bgc}">
    <div class="left" @click="$router.go(-1)">
      <slot name="go"><van-icon name="arrow-left" :color="leftColor"/></slot>
    </div>
    <div class="center" :style="{color: color, fontWeight: blob}">
        <slot name="title">会员登录</slot>
    </div>
  </div>
  </div>
</template>

<script>
export default {
  name: 'NavBarIndex',
  props: {
    color: {
      type: String,
      default: 'black'
    },
    bgc: {
      type: String,
      default: '#fff'
    },
    blob: {
      default: 400
    },
    leftColor: {
      default: 'black'
    }
  }
}
</script>

<style lang="less" scoped>
.boxTitle {
  z-index: 999;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  .title {
    height: 50px;
    position: relative;
    .left {
        position: absolute;
        top: 50%;
        left: 10px;
        transform: translate(0, -50%);
        font-size: 20px;
    }
    .center {
        text-align: center;
        line-height: 50px;
        font-size: 17px;
    }
}
}
</style>
