
import { getCartItem, updateCart, deleteCartItem } from '@/api/cart'
import { setStorage, getStorage } from '@/utils/storage'

import { Toast } from 'vant'
const state = {
  cartList: []
}
const mutations = {
  commitData (state, payload) {
    // 如果是数组则是获取所有列表
    if (payload instanceof Array) {
      state.cartList = payload
      // 如果是对象则是修改列表
    } else if (payload instanceof Object) {
      const arr = state.cartList.find(item => item.goods_id === payload.goodsId)
      arr.goods_num = payload.goodsNum
    }
  },
  // 修改复选框状态([localsorage])
  changeIsDecision (state, payload) {
    // storage同步修改
    const storageOldArr = getStorage('check')
    const newarr = state.cartList.find(item => payload.goodsId === item.id)

    newarr.isDecision = payload.bool ? 1 : 0
    const storageNewArr = storageOldArr.map(item => {
      if (item.id === payload.goodsId) {
        item.bool = payload.bool ? 1 : 0
      }
      return item
    })
    setStorage('check', storageNewArr)
  },
  modify (state, payload) {
    state.cartList.forEach(element => {
      element.isDecision = payload ? 1 : 0
    })

    const arr = getStorage('check').map(item => {
      item.bool = payload ? 1 : 0
      return item
    })
    setStorage('check', arr)
  }
}
const actions = {
/**
*@param {string} 这函数返回的是处理好的购物车车列表，包括永久存储购物车物品是否选中，如果没有存储则给每个物品设置为选中状态，如果存储则，id比对，有的则修改没有的选中状态为1
*/
  // 获取购物车列表
  async getCartGoods (context) {
    try {
      const { data: { list } } = await getCartItem()
      // 由于后台没有购物车选中字段无法判断购物车否无选中，需要添加新的字段来存储该信息且永久存储
      // **************************************************************************************************************************
      let storageArr = getStorage('check') || []
      const newArr = storageArr.length !== 0
        ? list.map(item => {
          const result = storageArr.find(item2 => item.id === item2.id)
          if (result) {
            item.isDecision = result.bool
          // 没有选中状态唯一，且加入localStorage
          } else {
            item.isDecision = 1
            storageArr.push({ id: item.id, bool: 1 })
          }
          return item
        })
        : list.map(item => {
          item.isDecision = 1
          storageArr.push({ id: item.id, bool: 1 })
          return item
        })
      // 把id不存在的筛掉
      storageArr = storageArr.filter(item => {
        const result = newArr.find(item2 => item.id === item2.id)
        return Boolean(result)
      })
      setStorage('check', storageArr)
      // **************************************************************************************************************************
      context.commit('commitData', newArr)
    } catch (error) {
      if (error === '缺少必要的参数token, 请先登录') return Toast('请先登录')
      Toast(error)
    }
  },
  async updateCartItem (context, payload) {
    // 发起请求
    const res = await updateCart(payload)
    // 更新状态
    if (res.status === 200) context.commit('commitData', payload)
    else Toast('更新失败')
  },
  async delCartItem (context) {
    if (context.getters.accountTotal === 0) return
    // 选中状态全部归零
    const arr = context.getters.isDecisionTotal.map(item => item.id)// 注意这个id不是goods_Id而是真正的购物车id

    const res = await deleteCartItem(arr)
    if (res.status === 200) {
      Toast('删除成功')
      context.dispatch('getCartGoods')
    }
  }
}
const getters = {
  // 物品选中状态的总和
  total (state, getters) {
    // 算出选择状态为1总和
    const result = getters.isDecisionTotal.reduce((count, item) => {
      return (item.goods.goods_price_max * item.goods_num) + count
    }, 0)
    return result.toFixed(2)
  },
  accountTotal (state, getters) {
    return getters.isDecisionTotal.length
  },
  isDecisionTotal (state) {
    return state.cartList.filter(item => item.isDecision === 1)
  }
}

export default {
  namespaced: true,
  state,
  mutations,
  actions,
  getters
}
