import store from '@/store'
const adressedit = () => import('@/views/adress/addandedit.vue')
const adress = () => import('@/views/adress/adressLIst.vue')

export default [
  {
    path: '/adresslist',
    component: adress,
    beforeEnter (to, from, next) {
      // 如果是跳转是pay的话保留之前的地址，方便点击编辑跳转
      if (from.path === '/pay') {
        store.state.addressTo = from
        // 如果是在pay页面跳转到list页面新增或添加跳转到此页面，点击默认地址还是跳转到pay页面，否则点击默认地址不跳转
      } else if (from.path === '/adressedit' && from.query.token === '888') {
        return next()
        // 其他页面就把存的页面重置，点击默认不跳转任何页面
      } else {
        store.state.addressTo = undefined
      }
      next()
    }
  },
  {
    path: '/adressedit',
    component: adressedit
  }
]
