import store from '@/store'
const login = () => import('@/views/login/login.vue')

export default [
  {
    path: '/login',
    component: login,
    beforeEnter: (to, from, next) => {
      // 保存login去的地方
      store.state.loginToUrl = from.fullPath

      // 直接放行
      next()
    }
  }
]
