import Vue from 'vue'
import VueRouter from 'vue-router'
import layout from '@/router/layout.js'
import login from '@/router/login.js'
import myorder from '@/router/myorder.js'
import pay from '@/router/pay.js'
import prodetail from '@/router/prodetail.js'
import search from '@/router/search'
import store from '@/store'
import adress from '@/router/adress.js'

Vue.use(VueRouter)

const routes = [
  ...layout,
  ...login,
  ...myorder,
  ...pay,
  ...prodetail,
  ...search,
  ...adress
]

const router = new VueRouter({
  routes
})
// 需要权限的数组
const privateArr = ['/pay', '/myroder']
router.beforeEach((to, from, next) => {
  if (privateArr.includes(to.path)) {
    store.state.user.userInfo ? next() : next('/login')
  } else {
    next()
  }
})

export default router
