import { setStorage, getStorage, removeStorage } from '@/utils/storage'
import router from '@/router'
const state = {
// 如果本地有直接获取，如果没有被赋值，并存入
  userInfo: getStorage('hm_shopping_info')
}
const mutations = {
  cmUserInfo (state, info) {
    state.userInfo = info
    // 存入本地
    setStorage('hm_shopping_info', info)
  }
}
const actions = {
  clearLogin (context) {
    // 清空vuex的store
    context.commit('cmUserInfo', null)
    // 清空购物车
    context.commit('cart/commitData', [], { root: true })// 使用访问根仓库
    removeStorage('check')
    removeStorage('hm_shopping_info')
    removeStorage('histroyRecode')
    router.go(0)
  }
}
const getters = {}

export default {
  namespaced: true,
  state,
  mutations,
  actions,
  getters
}
